import { useState, useEffect } from 'react'
import Navbar from '@/components/Navbar'
import HeroSection from '@/components/HeroSection'
import ProductCard, { ProductGrid, CategoryHeader } from '@/components/ProductCard'
import MiniCart from '@/components/MiniCart'
import { supabase } from '@/lib/supabase'

export default function Home() {
  const [products, setProducts] = useState([])
  const [categories, setCategories] = useState([])
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [session, setSession] = useState(null)
  const [loading, setLoading] = useState(true)
  
  // CART STATE
  const [cart, setCart] = useState([])
  const [isCartOpen, setIsCartOpen] = useState(false)

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      setSession(data.session)
    })
    loadProducts()
    loadCart()
  }, [])

  // Cart aus localStorage laden
  const loadCart = () => {
    const savedCart = localStorage.getItem('simonetti-cart')
    if (savedCart) {
      setCart(JSON.parse(savedCart))
    }
  }

  // Cart speichern
  const saveCart = (newCart) => {
    setCart(newCart)
    localStorage.setItem('simonetti-cart', JSON.stringify(newCart))
  }

  const loadProducts = async () => {
    setLoading(true)
    
    const { data } = await supabase
      .from('products')
      .select('*')
      .eq('active', true)
      .order('category', { ascending: true })

    if (data) {
      setProducts(data)
      const uniqueCategories = [...new Set(data.map(p => p.category))].filter(Boolean)
      setCategories(uniqueCategories)
    }
    
    setLoading(false)
  }

  // ADD TO CART
  const handleAddToCart = (product) => {
    const existingItem = cart.find(item => item.id === product.id)
    
    let newCart
    if (existingItem) {
      // Erh√∂he Menge
      newCart = cart.map(item =>
        item.id === product.id
          ? { ...item, quantity: item.quantity + 1 }
          : item
      )
    } else {
      // Neues Item
      newCart = [...cart, { ...product, quantity: 1 }]
    }
    
    saveCart(newCart)
    setIsCartOpen(true) // √ñffne Cart automatisch
  }

  // UPDATE QUANTITY
  const updateQuantity = (productId, newQuantity) => {
    if (newQuantity === 0) {
      removeFromCart(productId)
      return
    }
    
    const newCart = cart.map(item =>
      item.id === productId
        ? { ...item, quantity: newQuantity }
        : item
    )
    saveCart(newCart)
  }

  // REMOVE FROM CART
  const removeFromCart = (productId) => {
    const newCart = cart.filter(item => item.id !== productId)
    saveCart(newCart)
  }

  // CLEAR CART
  const clearCart = () => {
    saveCart([])
  }

  // CART TOTAL
  const cartTotal = cart.reduce((sum, item) => sum + (item.price * item.quantity), 0)
  const cartCount = cart.reduce((sum, item) => sum + item.quantity, 0)

  // Gefilterte Produkte
  const filteredProducts = selectedCategory === 'all' 
    ? products 
    : products.filter(p => p.category === selectedCategory)

  return (
    <div className="min-h-screen bg-white">
      <Navbar 
        session={session} 
        cartCount={cartCount} 
        onCartClick={() => setIsCartOpen(true)} 
      />
      
      <HeroSection />

      {/* MiniCart Sidebar */}
      <MiniCart
        isOpen={isCartOpen}
        onClose={() => setIsCartOpen(false)}
        cart={cart}
        onUpdateQuantity={updateQuantity}
        onRemove={removeFromCart}
        onClear={clearCart}
        total={cartTotal}
      />

      {/* Speisekarte */}
      <section id="speisekarte" className="py-24 bg-white">
        <div className="max-w-7xl mx-auto px-6">
          
          <CategoryHeader 
            title="Unsere Gelato-Sorten"
            description="T√§glich frisch hergestellt nach traditioneller italienischer Rezeptur"
          />

          {/* Kategorien-Filter */}
          {categories.length > 0 && (
            <div className="flex flex-wrap justify-center gap-3 mb-12">
              <button
                onClick={() => setSelectedCategory('all')}
                className={`px-6 py-2.5 text-sm font-medium uppercase tracking-wider transition ${
                  selectedCategory === 'all'
                    ? 'bg-black text-white'
                    : 'bg-white text-gray-700 border-2 border-gray-200 hover:border-gray-400'
                }`}
              >
                Alle ({products.length})
              </button>
              
              {categories.map(category => {
                const count = products.filter(p => p.category === category).length
                return (
                  <button
                    key={category}
                    onClick={() => setSelectedCategory(category)}
                    className={`px-6 py-2.5 text-sm font-medium uppercase tracking-wider transition ${
                      selectedCategory === category
                        ? 'bg-black text-white'
                        : 'bg-white text-gray-700 border-2 border-gray-200 hover:border-gray-400'
                    }`}
                  >
                    {category} ({count})
                  </button>
                )
              })}
            </div>
          )}

          {/* Produkte */}
          {loading ? (
            <div className="text-center py-12">
              <div className="text-6xl mb-4 animate-pulse">üç¶</div>
              <p className="text-gray-600">L√§dt Produkte...</p>
            </div>
          ) : filteredProducts.length === 0 ? (
            <div className="text-center py-12">
              <div className="text-6xl mb-4">üòî</div>
              <p className="text-gray-600 text-xl">Keine Produkte in dieser Kategorie</p>
            </div>
          ) : (
            <ProductGrid>
              {filteredProducts.map(product => (
                <ProductCard
                  key={product.id}
                  product={product}
                  onAddToCart={() => handleAddToCart(product)}
                />
              ))}
            </ProductGrid>
          )}

        </div>
      </section>

      {/* Footer */}
      <footer className="bg-[#1a1a1a] text-white py-16">
        <div className="max-w-7xl mx-auto px-6">
          <div className="grid md:grid-cols-3 gap-12">
            
            <div>
              <h3 className="font-display text-2xl font-bold mb-4">Simonetti</h3>
              <p className="text-gray-400">
                Italienisches Gelato aus Liebe zum Handwerk
              </p>
            </div>

            <div>
              <h4 className="font-semibold mb-4">√ñffnungszeiten</h4>
              <p className="text-gray-400 text-sm">
                Mo - Sa: 09:00 - 19:00 Uhr<br />
                So: 13:00 - 19:00 Uhr
              </p>
            </div>

            <div>
              <h4 className="font-semibold mb-4">Kontakt</h4>
              <p className="text-gray-400 text-sm">
                Konrad-Adenauer-Platz 2<br />
                40764 Langenfeld<br />
                Tel: 02173 1622780
              </p>
            </div>

          </div>

          <div className="border-t border-gray-800 mt-12 pt-8 text-center text-sm text-gray-500">
            ¬© 2026 Eiscafe Simonetti. Alle Rechte vorbehalten.
          </div>
        </div>
      </footer>
    </div>
  )
}